import React, { useState, useEffect } from 'react';
import axios from 'axios';
import t from 'typy';
import { LoadingBox, base_url } from './helper';
import setAuthToken from './misc/setAuthToken';

export default function Login(props) {
  const [, forceUpdate] = useState();
  const [error, setError] = useState(false);
  const [password, setPassword] = useState('');
  const [username, setUsername] = useState('');
  const [loading, setLoading] = useState(false);
  const [roles, setRole] = useState('faq');

  useEffect(() => {

  }, [error])

  const get_login = (e) => {
    e.preventDefault();
    setLoading(true);
    setError(false);
    login();
  }

  const login = () => {
    const form_data = {
      email: username,
      password: password,
    }
    axios.post(base_url("users/login", roles), form_data).then(res => {
      setLoading(false);
      console.log(res.data)
      if (res.data.meta.status_code === 200) {
        let user_credentials = (t(res, 'data.data.user_credentials').isDefined) ? res.data.data.user_credentials : res.data.data.credentials;
        localStorage.setItem('ghtw', res.data.data.token);
        localStorage.setItem('hgfs', res.data.data._id);
        localStorage.setItem('gfhf', res.data.data._id);
        localStorage.setItem('bcvb', "");
        localStorage.setItem('fddg', "");
        localStorage.setItem('extuuii', user_credentials.role);
        localStorage.setItem('name', user_credentials.username);
        switch (user_credentials.role) {
          case 'faq':
            props.history.push("/faq/list_article");
            break;
          case 'blog':
            props.history.push("/blog/list_article");
            break;
          default:
            break;
        }
        setAuthToken(res.data.data.token);
      }
    }).catch(err => {
      setLoading(false);
      console.log(err);
      if (t(err, 'response.data.meta.message').isDefined) {
        setError(err.response.data.meta.message);
      }
      if (t(err, 'response.data.email').isDefined) {
        setError(err.response.data.email);
      }
      if (t(err, 'response.data.password').isDefined) {
        setError(err.response.data.password);
      }
    })
  }

  return (
    <div className="login-page">
      <div className="login-box position-relative">
        {loading && <LoadingBox />}
        <div className="login-logo">
          <a><i className="fal fa-user"></i> Marketkoin</a>
        </div>
        <div className="card">
          <div className="card-body login-card-body">
            <p className="login-box-msg">Please sign in</p>

            <div id="alert" className={`alert p-2 alert-warning ${error ? '' : 'hidden'}`}><small>{error}</small></div>

            <form onSubmit={get_login}>
              <div className="input-group-custom">
                <input type="text" className="form-control" value={username} onChange={(e) => setUsername(e.target.value)} name="username" placeholder="Username or email" />
                <span className="fa fa-user icon"></span>
              </div>
              <div className="input-group-custom">
                <input type="password" className="form-control" onChange={(e) => setPassword(e.target.value)} name="password" placeholder="Password" />
                <span className="fa fa-lock icon"></span>
              </div>
              <div className="input-group-custom text-center">
                <div className="text-white mb-2"><b>Login as</b></div>
                <button style={{ height: '25px', borderRadius: '3px' }} type="button" onClick={() => setRole('faq')} className={`btn btn-sm mr-2 btn-${roles == 'faq' ? 'primary' : 'default'}`}>Faq</button>
                <button style={{ height: '25px', borderRadius: '3px' }} type="button" onClick={() => setRole('blog')} className={`btn btn-sm btn-${roles == 'blog' ? 'primary' : 'default'}`}>Blog</button>
              </div>
              <div className="row">
                <div className="col-12">
                  <button disabled={(username == '' || password == '' || password.length < 8)} type="submit" className="btn btn-warning btn-block mb-1 btn-login">
                    <span className="float-right icon">
                      <i className="fas fa-sign-in"></i>
                    </span>
                    <span className="float-left text">Sign in</span>
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div >
  );
} 